
import React, { useState } from 'react';
import { PDFDocument } from 'pdf-lib';
import { Viewer, Worker } from '@react-pdf-viewer/core';
import "@react-pdf-viewer/core/lib/styles/index.css";


const CombinarPDF = () => {

    const [pdfFiles, setPdfFiles] = useState([]);
    const [combinedPdf, setCombinedPdf] = useState(null);

    const handleFileSelection = async (e) => {
        const files = Array.from(e.target.files).filter((file) => file.name.endsWith(".pdf"));

        if (files.length > 3) {
            alert("Por favor, selecciona solamente los 3 archivos PDF necesarios");
        } else {
            setPdfFiles(files);
        }
    };

    const combinePdfs = async () => {
        const pdfDoc = await PDFDocument.create();

        for (let file of pdfFiles) {
            const arrayBuffer = await file.arrayBuffer();
            const loadedPdf = await PDFDocument.load(arrayBuffer);
            const copiedPages = await pdfDoc.copyPages(loadedPdf, loadedPdf.getPageIndices());
            copiedPages.forEach((page) => pdfDoc.addPage(page));
        }

        const pdfBytes = await pdfDoc.save();
        const blob = new Blob([pdfBytes], { type: "application/pdf" });
        setCombinedPdf(URL.createObjectURL(blob));
    };

    const saveCombinedPDF = async () => {
        if (!combinedPdf) return;
    
        const handle = await window.showSaveFilePicker({
          suggestedName: "combined.pdf",
          types: [
            {
              description: "PDF Document",
              accept: { "application/pdf": [".pdf"] },
            },
          ],
        });
    
        const writable = await handle.createWritable();
        await writable.write(await fetch(combinedPdf).then((res) => res.blob()));
        await writable.close();
    
        alert("Archivo guardado con xito.");
      };
    


  return (
    <div>
        <h2>Combina archivos .pdf</h2>
        <input
            type="file"
            accept= "application/pdf"
            multiple
            onChange={handleFileSelection}
        />
        <button onClick={combinePdfs} disabled={pdfFiles.length !== 3}>
            Combinar
        </button>
        {
            combinedPdf && (
                <button onClick={saveCombinedPDF}>Guardar</button>
            )
        }
        <div style={{ display: "flex", gap: "10px", marginTop: "20px" }}>
            {
                pdfFiles.map((file, index) => (
                    <div key={index} style={{ width: "200px", height: "300px" }}>
                        <Worker workerUrl={`https://unpkg.com/pdfjs-dist@3.11.174/build/pdf.worker.min.js`}>
                            <Viewer fileUrl={URL.createObjectURL(file)} />
                        </Worker>
                    </div>
                ))
            }
        </div>
        
    </div>
  )
};

export default CombinarPDF